import { Component } from '@angular/core';
import { CommentsService } from './comments.service';
import { FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [CommentsService]
})
export class AppComponent {
  title = 'bit-tutorial-frontend';

  comments = [];
  checkoutForm;

  constructor(
    private commentsService: CommentsService,
    private formBuilder: FormBuilder
  ) {
    this.checkoutForm = this.formBuilder.group({
      title: '',
      text: ''
    });
  }

  public async ngOnInit() {
    this.comments = await this.commentsService.getAllComments();
  }

  public async createComment(comment) {
    const commentRes = await this.commentsService.createComment(comment);
    this.comments.push(commentRes);
  }

  public async deleteComment(id) {
    await this.commentsService.deleteComment(id);
    this.comments = await this.commentsService.getAllComments();
  }
}
