import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CommentsService {

  baseUrl;
  ready: Promise<any>;

  constructor(
    private http: HttpClient
  ) {
    this.ready = http.get('/assets/config.json').toPromise().then((res: any) => {
      this.baseUrl = res.baseUrl;
    });
  }

  public async getAllComments(): Promise<any> {
    await this.ready;
    const res = await this.http.get(this.baseUrl + '/api/comments').toPromise();
    return res;
  }

  public async createComment(comment) {
    await this.ready;
    const res = await this.http.post(this.baseUrl + '/api/comments', comment).toPromise();
    return res;
  }

  public async deleteComment(id) {
    await this.ready;
    await this.http.delete(this.baseUrl + '/api/comments/' + id).toPromise();
  }
}
